.k-spreadsheet-sheets-items .k-tabstrip-items-wrapper div {
    box-sizing: border-box;
    outline: 0;
    display: flex;
    flex-direction: inherit;
    flex-wrap: wrap;
    flex: 1 1 auto;
    gap: 0;
    list-style: none;
}

.k-spreadsheet-sheets-items .k-item .k-link {
    padding-block: 6px;
    padding-inline: 12px;
    color: inherit;
    cursor: pointer;
    display: inline-flex;
    vertical-align: middle;
    flex: 1 1 auto;
    flex-direction: row;
    align-content: center;
    align-items: center;
}

.k-spreadsheet-sheets-items .k-item {
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
    position: relative;
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-items: stretch;
    outline: 0;
}